.mc__comm {
    background-color: rgb(245, 245, 245);
    padding: 8rem 4rem;
}

.mc__comm-container {
    max-width: 70rem;
    /* min-width: 60rem; */
    display: flex;
    flex-direction: row;
    margin: auto;
}

.mc__comm-container_content {
}

.mc__comm-container_content h1 {
    font-weight: 700;
    line-height: 120%;
    font-size: 48px;
}

.mc__comm-container_content p {
    font-size: 25px;
    line-height: 1.5;
}

.mc__comm-container_img {
    display: flex;
    flex-direction: column;
    padding-left: 7rem;
}

.mc__comm-container_img img {
    border-radius: 1rem;
    width: 30rem;
    height: 30rem;
}

.mc__comm-container_img2 {
    display: none;
}
@media screen and (max-width: 1120px) {
    .mc__comm-container_img {
        align-self: center;
        padding-left: 7vw;
    }
    .mc__comm-container_img img {
        width: 40vw;
        height: 40vw;
    }
    .mc__comm-container_content h1 {
        font-size: 4vw;
    }
    .mc__comm-container_content p {
        font-size: 2vw;
    }
}

@media screen and (max-width: 700px) {

    .mc__comm-container {
        flex-direction: column;
    }
    .mc__comm-container_img {
        padding-left: 0;
    }
    .mc__comm-container_img img {
        width: 100%;
        height: 100%;
    }
    .mc__comm-container_content h1 {
        font-size: 6vw;
    }
    .mc__comm-container_content p {
        font-size: 4vw;
    }
    .mc__comm-container_img2 {
        display: flex;
    }
    .mc__comm-container_img1 {
        display: none;
    }
}

@media screen and (max-width: 414px) {
    .mc__comm {
        padding: 4rem 2.5rem;
    }
    .mc__comm-container_content h1 {
        font-size: 35px;
    }
    .mc__comm-container_content p {
        font-size: 20px;
    }
    .mc__comm-container_img {
        padding-bottom: 2rem;
    }

}

