.mc__play {
    background-color: #fff;
    margin: 8rem 4rem;
}

.mc__play-container {
    max-width: 70rem;
    /* min-width: 60rem; */
    display: flex;
    flex-direction: row;
    margin: auto;
}

.mc__play-container_content {
}

.mc__play-container_content h1 {
    font-weight: 700;
    line-height: 120%;
    font-size: 48px;
}

.mc__play-container_content p {
    font-size: 25px;
    line-height: 1.5;
}

.mc__play-container_img {
    display: flex;
    flex-direction: column;
    padding-right: 7rem;
}

.mc__play-container_img img {
    border-radius: 1rem;
    width: 30rem;
    height: 30rem;
}

@media screen and (max-width: 1120px) {
    .mc__play-container_img {
        align-self: center;
        padding-right: 7vw;
    }
    .mc__play-container_img img {
        width: 40vw;
        height: 40vw;
    }
    .mc__play-container_content h1 {
        font-size: 4vw;
    }
    .mc__play-container_content p {
        font-size: 2vw;
    }
}

@media screen and (max-width: 700px) {
    .mc__play-container {
        flex-direction: column;
    }
    .mc__play-container_img {
        padding-right: 0;
    }
    .mc__play-container_img img {
        width: 100%;
        height: 100%;
    }
    .mc__play-container_content h1 {
        font-size: 6vw;
    }
    .mc__play-container_content p {
        font-size: 4vw;
    }
    
}

@media screen and (max-width: 414px) {
    .mc__play {
        margin: 4rem 2.5rem;
    }
    .mc__play-container_content h1 {
        font-size: 35px;
    }
    .mc__play-container_content p {
        font-size: 20px;
    }
    .mc__play-container_img {
        padding-bottom: 2rem;
    }

}