.mc__header {
    text-align: center;
    background-image: url('../../assets/mythos.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin-top: 5rem;
    background-attachment:fixed;
    
}

.mc__header-container {
    padding: 7rem 14rem;
    padding-top: 13rem;
    padding-bottom: 1rem;
}
.mc__header-container_heading h1 {
    font-size: 80px;
    font-weight: 900;
    color: white;

}

.mc__header-container_content p {
    font-size: 60px;
    font-weight: 500;
    color: white;
}

.mc__header-container button {
    cursor: pointer;
    background-color: #58cc02;
    box-shadow: 0 5px 0 #58a700;
    border: none;
    padding: 0.9rem 1.5rem;
    padding-top: 1.2rem;
    /* border-radius: 20px; */
    color: white;
    font-weight: bolder;
    font-size: 22px;
}

.mc__header-container svg {
    vertical-align: middle;
    padding-bottom: 0.3rem;
}

.mc__header-container button:active {
    box-shadow: none;
    transform: translateY(5px);
}

.mc__header-container_button {
    display: none;
    align-items: center;
    text-align: center;
}

@media screen and (max-width: 600px) {
    .mc__header {
        margin-top: 0rem;
        background-image: url('../../assets/mythos.png');
        background-position: left;
    }
    .mc__header-container {
        padding: 12rem 2rem;
        padding-bottom: 7rem;
        text-align: left;
    }
    .mc__header-container_heading h1 {
        font-size: 45px;
    }
    .mc__header-container_content p {
        font-size: 25px;
    }

    .mc__header-container_button {
        display: block;
        align-items: center;
        text-align: center;
    }
}
