.mc__team {
    background-color: #fff;
    margin: 8rem 4rem;
}

.mc__team-container {
    max-width: 70rem;
    /* min-width: 60rem; */
    display: flex;
    flex-direction: row;
    margin: auto;
}

.mc__team-container_content {
}

.mc__team-container_content h1 {
    font-weight: 700;
    line-height: 120%;
    font-size: 48px;
}

.mc__team-container_content p {
    font-size: 25px;
    line-height: 1.5;
}

.mc__team-container_img {
    display: flex;
    flex-direction: column;
    align-self: center;
    padding-right: 7rem;
}

.mc__team-container_img img {
    border-radius: 1rem;
    width: 30rem;
    height: 30rem;
}

.mc__team-container button {
    cursor: pointer;
    background-color: #58cc02;
    box-shadow: 0 5px 0 #58a700;
    border: none;
    padding: 0.9rem 1.5rem;
    padding-top: 1.2rem;
    /* border-radius: 20px; */
    color: white;
    font-weight: bolder;
    font-size: 22px;
    width: 100%;
}

.mc__team-container button:active {
    box-shadow: none;
    transform: translateY(5px);
}
@media screen and (max-width: 1120px) {
    .mc__team-container_img {
        align-self: center;
        padding-right: 7vw;
    }
    .mc__team-container_img img {
        width: 40vw;
        height: 40vw;
    }
    .mc__team-container_content h1 {
        font-size: 4vw;
    }
    .mc__team-container_content p {
        font-size: 2vw;
    }
}

@media screen and (max-width: 700px) {
    .mc__team-container {
        flex-direction: column;
    }
    .mc__team-container_img {
        padding-right: 0;
    }
    .mc__team-container_img img {
        width: 100%;
        height: 100%;
    }
    .mc__team-container_content h1 {
        font-size: 6vw;
    }
    .mc__team-container_content p {
        font-size: 4vw;
    }
    
}

@media screen and (max-width: 414px) {
    .mc__team {
        margin: 4rem 2.5rem;
    }
    .mc__team-container_content h1 {
        font-size: 35px;
    }
    .mc__team-container_content p {
        font-size: 20px;
    }
    .mc__team-container_img {
        padding-bottom: 2rem;
    }

}