.mc__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 6rem;
    background:#313131;
    position: fixed;
    left: 0;
    top: 0;
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    right: 0;
}

.mc__navbar-links_logo img {
    vertical-align: middle;
    height: 19px;
    padding-right: 3rem;
}

.mc__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.mc__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.mc__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.mc__navbar-links_container a,
.mc__navbar-menu_container a {
    display: block;
    color: white;
    text-align: center;
    padding: 0rem 2rem;
    font-size: 18px;
    text-decoration: none;
    font-weight: bold;
}

.mc__navbar-right {
    display: flex;
    flex-direction: row;
}

.mc__navbar-right_play {
    padding-right: 4rem;
}
.mc__navbar-right_play a {
    vertical-align: middle;
    font-size: 18px;
    color: white;
    font-weight: bold;
}

.mc__navbar-right-discord img {
    vertical-align: middle;
    width: 30px;
}

.mc__navbar-right_play svg {
    padding-left: 0.5rem;
    vertical-align: middle;
    padding-bottom: 0.2rem;
}

.mc__navbar-right_play button {
    cursor: pointer;
    background: none;
    border: none;
}


.mc__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.mc__navbar-menu svg {
    padding-left: 1rem;
    cursor: pointer;
}

.mc__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    background-color: rgb(87, 87, 87);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: -2rem;
    margin-top: 2rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
}

.mc__navbar-menu_container a {
    margin: 1.5rem 0;
    text-align: end;

}

.mc__navbar-menu_container svg {
    margin-left: -1rem;
}

.mc__navbar-menu_container-links-discord img {
    vertical-align: middle;
    width: 30px;

}

.mc__navbar-menu_container-links-discord {
    padding: 0 0;
    margin-top: -1.5rem;
}



@media screen and (max-width: 1400px) {
    .mc__navbar {
        padding: 2rem 6rem;
        padding-right: 3rem;
    }
    .mc__navbar-menu {
        display: flex;
    }
    .mc__navbar-links_container {
        display: none;
    }
    .mc__navbar-menu_container-links-discord {
        display: none;
    }
}

@media screen and (max-width: 748px) {
    .mc__navbar {
        padding: 2rem 6rem;
        padding-right: 3rem;
    }
    .mc__navbar-menu_container-links-discord {
        display: flex;
    }
    .mc__navbar-right-discord {
        display: none;
    }
    .mc__navbar-right_play {
        margin-right: -5rem;
    }
}

@media screen and (max-width: 414px) {
    .mc__navbar {
        padding: 2rem 2rem;
        padding-left: 3rem;
    }
    .mc__navbar-right_play {
        display: none;
    }
    .mc__navbar-menu_container {
        display: block;
        justify-content: center;
        align-items: center;
        padding: 0rem 0;
        margin: 0rem 0rem;
        position: absolute;
        top: 3.7rem;
        left: -21.5rem;
        right: -2rem;
        border-radius: 0px;
        box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.4);
        background: rgb(73,73,73);
        background: linear-gradient(135deg, rgba(73,73,73,1) 0%, rgba(74,74,74,1) 100%);
    }
    .mc__navbar-menu_container a {
        margin: 0rem 0rem;
        padding: 1.2rem 0;
        text-align: center;
        font-size: 20px;
        left: 0;
        right: 0;
    }
    .mc__navbar-menu_container p {
        margin: 0rem 0rem;
        padding: 0rem 0;
    }
    .mc__navbar-menu_container a:hover {
        background-color: #58a700;
    }
}